*, *::before, *::after {
  box-sizing: border-box;
  font-family: inherit;
  font-size: 100%;
  margin: 0;
  padding: 0;
}

html:focus-within {
  scroll-behavior: smooth;
}

a, a:visited, a:hover {
  text-decoration: none;
  color: inherit;
}

input,
button,
textarea,
select {
  font: inherit;
}

ul, li {
  list-style: none;
}

button {
  background: transparent;
  border: none;
  color: inherit;
  cursor: pointer;
}

body {
  font-family: var(--ff);
  font-size: var(--fs-small);
  font-weight: var(--fw-reg);
  line-height: 1.5;
  background-color: var(--bg-clr);
  color: var(--text-clr);
  background-image: var(--bg-mobile);
  background-repeat: no-repeat;
  background-size: 100%;
}
@media (min-width: 40em) {
  body {
    font-size: var(--fs-body);
  }
}
@media (min-width: 71.875em) {
  body {
    background-image: var(--bg-desktop);
  }
}

.container {
  max-width: 33.75rem;
  margin: 0 auto;
  padding: 1.5625rem;
  padding-top: 3rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
    width: 100vw;
    height: 100vh;
    margin-top: 80px;
  }
}

.flex {
  display: flex;
  justify-content: space-between;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
.sr-only {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

:root {
  --ff:"Josefin Sans", sans-serif;
  --fs-xlarge: 2.5rem;
  --fs-large: 1.375rem;
  --fs-body: 1.125rem;
  --fs-med: 1rem;
  --fs-small: .875rem;
  --fs-xsmall: .75rem;
  --fs-xxsmall: .625rem;
  --fw-reg:400;
  --fw-bold:700;
  --accent-clr-BrightBlue: hsl(220, 98%, 61%);
  --VeryLightGray: hsl(0, 0%, 98%);
  --VeryLightGrayishBlue: hsl(236, 33%, 92%);
  --LightGrayishBlue: hsl(233, 11%, 84%);
  --DarkGrayishBlue: hsl(236, 9%, 61%);
  --VeryDarkGrayishBlue: hsl(235, 19%, 35%);
  --White: hsl(0, 0%, 100%);
  --VeryDarkBlue: hsl(235, 21%, 11%);
  --VeryDarkDesaturatedBlue: hsl(235, 24%, 19%);
  --LightGrayishBlue: hsl(234, 39%, 85%);
  --LightGrayishBlue-hover: hsl(236, 33%, 92%);
  --DarkGrayishBlue: hsl(234, 11%, 52%);
  --VeryDarkGrayishBlue: hsl(233, 14%, 35%);
  --VeryDarkGrayishBlue: hsl(237, 14%, 26%);
}

:root[data-theme=light] {
  --bg-clr: var(--VeryLightGray);
  --list-bg-clr: var(--White);
  --text-clr-xlight: var(--LightGrayishBlue);
  --text-clr-light: var(--DarkGrayishBlue);
  --text-clr: var(--VeryDarkGrayishBlue);
  --bg-mobile: url(/images/bg-mobile-light.jpg) ;
  --bg-desktop: url(/images/bg-desktop-light.jpg) ;
}

:root[data-theme=dark] {
  --bg-clr: var(--VeryDarkBlue);
  --list-bg-clr: var(--VeryDarkDesaturatedBlue);
  --text-clr-xlight: var(--DarkGrayishBlue);
  --text-clr-light: var(--LightGrayishBlue);
  --text-clr: var(--LightGrayishBlue-hover);
  --bg-mobile: url(/images/bg-mobile-dark.jpg) ;
  --bg-desktop: url(/images/bg-desktop-dark.jpg) ;
}

.header {
  align-items: center;
  line-height: 1.7;
  margin-bottom: 1.5625rem;
}
@media (min-width: 40em) {
  .header {
    margin-bottom: 2.1875rem;
  }
}
.header__title {
  font-size: var(--fs-large);
  text-transform: uppercase;
  color: var(--White);
  letter-spacing: 0.5rem;
}
@media (min-width: 40em) {
  .header__title {
    font-size: var(--fs-xlarge);
    letter-spacing: 1rem;
  }
}
.header__toggle {
  height: 1.375rem;
  width: 1.375rem;
}
@media (min-width: 40em) {
  .header__toggle {
    height: 1.75rem;
    width: 1.75rem;
  }
}
.header .themeButton {
  background-image: url(/images/icon-moon.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}

.todo__new-todo input, .todo__list {
  background-color: var(--White);
  border: none;
  border-radius: 0.4375rem;
  padding: 1rem 1.25rem;
  width: 100%;
  margin-bottom: 1rem;
}
.todo__new-todo input::placeholder {
  color: var(--text-clr-light);
}
.todo__task label {
  order: 2;
}